.header-container {
  padding: 20px;
}

.header-nav {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.header-nav > :not(:last-child) {
  margin-right: 20px;
}

.header-link {
  font-size: 14px;
}

.header-link:hover, .header-link:focus {
  text-decoration: underline;
}

.header-button-login {
  color: #fff;
  min-width: 96px;
  cursor: pointer;
  background-color: #1a73e8;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 10px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  transition: all .2s ease-in-out;
}

.header-button-login:hover {
  background-color: #1b66c9;
  box-shadow: 0 1px 3px 1px rgba(66, 64, 67, .15), 0 1px 2px rgba(60, 64, 67, .3);
}

.header-button-login:focus {
  background-color: #1c5fba;
  box-shadow: 0 1px 3px 1px rgba(66, 64, 67, .15), 0 1px 2px rgba(60, 64, 67, .3);
}

.header-button-login:active {
  background-color: #1b63c1;
  box-shadow: 0 1px 3px 1px rgba(66, 64, 67, .15), 0 1px 2px rgba(60, 64, 67, .3);
}

.header-button-menu {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: all .2s ease-in-out;
  display: flex;
}

.header-button-menu:hover {
  background-color: rgba(60, 64, 67, .08);
}

.icon-menu {
  fill: #5f6368;
}

